.solution{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    row-gap: 30px;
    column-gap: 30px;
    align-items: center;
    justify-content: space-between;
    width: 80%;
}
@media screen {
    .solution{
        justify-content: center;
    }
}
.solution.visible{
    visibility: visible;
    margin: 5%;
}
.solution.hidden{
    visibility: hidden;
}

.ticket{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-radius: 10px;
    padding: 10px;
}

.ticket.settimanali{
    width: 150px;
    background-color: aquamarine;
}
.ticket.mensili{
    width: 175px;
    background-color: blue;
    color: white;
}
.ticket.carnet{
    width: 150px;
    background-color: rgb(255, 0, 0);
    color: rgb(246, 246, 246);
}
.ticket.biglietti-semplici{
    width: 150px;
    background-color: rgb(5, 156, 17);
    color: white;
}