table{
    width: 100%;
    border-collapse: collapse;
}
table td{
    border: 1px solid #ca1919;
    font-size: 21px;
}
thead{
    background-color: #f2f2f2;
    text-align: center;
    font-weight: bold;
    font-size: 1.2em;
}
thead td{
    border: 1px solid #ddd;
    border-collapse: collapse;
}

tbody tr td{
    text-align: center;
    width: 70px;
    height: 70px;
    border: 1px solid #ddd;
    border-collapse: collapse;
}
td:hover, td.active{
    background-color: #fff717;
    cursor: pointer;
}
td.next-month{
    color: #f00000dd;
}
table td.empty{
    border: none;
}

.refresh-button{
    cursor: pointer;
    margin-bottom: 30px;
    border-radius: 10px;
    background-color: rgb(150, 27, 150);
    padding: 10px;
    color: white
}