form{
    width: 60%;
    max-width: 600px;
    align-self: center;
    display: flex;
    flex-direction: column;
    row-gap: 30px;
    padding: 5%;
    border: 1px solid rgb(68, 61, 61);
    background-color: rgb(13, 171, 79);
    border-radius: 10px;
    margin-top: 30px;
    margin-bottom: 30px;
}
input, label{
    font-size: 18px;
}
button[type="submit"]{
    height: 40px;
    border-radius: 10px;
    border: 1px solid coral;
    background: coral;
    font-size: 18px;
    color: white;
    cursor: pointer;
}
.form button{
width: 200px;
}
.field-box{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
   column-gap: 30px;
   width: 100%;
}
@media screen and (max-width: 768px) {
    .field-box{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: 15px;
    }
    .field-box label.yellow, .field-box label{
        text-align: center;
        width: 100%;
    }
    .input-box input[type="number"], .input-box input[type="text"]{
        width: 80%;
    }
    
}
.input-box{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    row-gap: 30px;
    height: 40px;
    width: 50%;
}
.input-box input[type="number"], .input-box input[type="text"], .field-box label, .input-box{
    width: 100%;
}
.field-box input[type="checkbox"]{
    margin-left: 20px;
    align-self: center;
}
.field-box label{
    text-align: center;
    padding: 10px 0px 10px 0;
    border: 1px solid none;
    background-color: rgb(228, 78, 78);
    color: white;
    border-radius: 5px;
}
.field-box label.yellow{
    background-color: rgb(255, 255, 0);
    color: black;
}
input{
    border-radius: 5px;
    border: 1px solid #ddd;
    text-align: center;
}